  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-shadow: 0px 1px 4px rgba(0, 0, 0, 0.12);
  background-image: linear-gradient(142.39deg, #FFC341 20.46%, #FFD701 65.31%);
  border: none;
  border-radius: 80px;
  cursor: pointer; }
  .button:hover, .button:focus {
    background-image: linear-gradient(143.55deg, #FFC341 -50.3%, #FFD701 63.1%); }
  .button:active {
    background-image: radial-gradient(150% 119.49% at 50% 50.85%, rgba(0, 0, 0, 0) 30.67%, rgba(3, 3, 3, 0.24) 100%), linear-gradient(143.55deg, #FFC341 -50.3%, #FFD701 63.1%); }

.input {
  display: block;
  min-width: 250px;
  padding: 19px 30px 20px 40px;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 20px;
  font-weight: 500;
  font-style: normal;
  text-align: left;
  color: #484848;
  text-transform: uppercase;
  background-color: transparent;
  background-position: 60px 40px;
  background-repeat: no-repeat;
  border: 2px solid #E3E3E3;
  border-radius: 80px; }
  .input::placeholder {
    color: rgba(72, 72, 72, 0.5); }
  .input:valid {
    border-color: #484848; }
  .input:hover, .input:focus {
    border-color: #FFC341;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); }
  .input:invalid {
    border-color: #FF0000; }
  .input--tel:valid {
