  .main-banner::before {
    content: "";
    position: absolute;
    top: -94px;
    left: 0;
    z-index: -1;
    display: block;
    width: 100%;
    height: 876px;
    background-image: url("../img/banner-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: 1266px 906px;
    background-position: center 0; }
    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
